import React, { useRef, forwardRef, useImperativeHandle } from 'react';
import clsx from 'clsx';
import {
    StepperIndicatorPropTypes,
    StepperIndicatorDefaultProps
} from './generated/propTypes';

const StepperIndicator = forwardRef((props, extRef) => {
    const { totalSteps, activeStep, ...customProps } = props;

    const ref = useRef();

    useImperativeHandle(extRef, () => ref.current, []);

    return (
        <div
            ref={ref}
            className='lmnt lmnt-stepper-indicator__container'
            {...customProps}
        >
            {[...Array(totalSteps)].map((x, index) => (
                <div
                    className={clsx(
                        'lmnt-stepper-indicator__item',
                        index + 1 === activeStep &&
                            'lmnt-stepper-indicator__item--active'
                    )}
                    // eslint-disable-next-line react/no-array-index-key
                    key={`lmnt-stepper-indicator__item-${index}`}
                />
            ))}
        </div>
    );
});

StepperIndicator.displayName = 'StepperIndicator';
StepperIndicator.propTypes = StepperIndicatorPropTypes;
StepperIndicator.defaultProps = StepperIndicatorDefaultProps;

export default StepperIndicator;
